class Prices extends React.Component {
    state = {
        character : ''
    }
    getCharDetails = (e) => {
            let c = this.props.chars.filter(char => char.name === e.target.value)
            this.setState({ character : c })
    }
    render() {
        return(
            <div>
                <select className="form-control" onChange={this.getCharDetails}>
                    <option value="Luke Skywalker">Luke Skywalker</option>
                    <option value="C-3PO">C-3PO</option>
                    <option value="R2-D2">R2-D2</option>
                    <option value="Darth Vader">Darth Vader</option>
                    <option value="Leia Organa">Leia Organa</option>
                    <option value="Owen Lars">Owen Lars</option>
                </select>
                <br/>
                <div class="card border-info mb-3" style="max-width: 20rem;">
                    <div class="card-header">{this.state.character.name}</div>
                    <div class="card-body">
                        <h4 class="card-title">Info card title</h4>
                        <p class="card-text">Some quick example text to build on the card title and make up the bulk of the card's content.</p>
                    </div>
                </div>
                
                
            </div>
        )
    }
};

export default Prices;